
.page-item{display:inline-block;border:1px solid #e3e7ee;color:#8e99b3;border-radius:4px;font-size:12px;-webkit-text-size-adjust:none;line-height:32px;width:32px;height:32px;text-align:center;vertical-align:middle;cursor:pointer;margin-right:4px
}
.page-item.active{background-color:#1f95e7;color:#fff
}
.page-item:hover:not(.active){background-color:#fafafe
}
.page-item .prev i,.page-item .next i{background:url("../../assets/sprite.png");width:11px;height:18px;display:inline-block;cursor:pointer;vertical-align:middle
}
.page-item .prev i{background-position:-25px -3px
}
.page-item .next i{background-position:-40px -3px
}


